<template>
  <div class="w-full overflow-x-auto">
    <table class="bb-table">
      <BBTHead :selectable="selectable" :value="checkValue" :total="total" :header="header" @check-all="checkAll" :check-all="isCheckAll" @sortTHead="handleSort" :disabled="disabled" />
      <template v-for="(item, i) in items" :key="item">
        <BBTBody class="group" :class="{ 'cursor-pointer': children && item[children].length > 0 }" @click="action[i] = !disableExpand ? !action[i] : action[i]">
          <td v-if="selectable && !children" class="bb-table-column">
            <Checkbox v-model="checkValue" color="teal" :value="item.id" value-off="0" :disabled="disabled" @change="valueChange" />
          </td>
          <td v-if="selectable && (children && item[children].length == 0)" class="bb-table-column">
            <Checkbox v-model="checkValue" color="teal" :value="item.id" value-off="0" :disabled="disabled" @change="valueChange" />
          </td>
          <td v-if="children && item[children].length > 0" class="bb-table-column">
            <div
								:class="{ 'el-table__expand-icon el-table__expand-icon--expanded': action[i], 'el-table__expand-icon': !action[i]}"
								>
								<i class="el-icon-arrow-right"></i>
							</div>
          </td>
          <slot name="item" :item="item" :collapsed="children ? item[children].length > 0 ? action[i] : false : action[i]" />
        </BBTBody>
        <template v-if="children">
          <BBTBody v-for="child in item[children]" :key="child" class="group" :style="{ display: action[i] ? 'table-row' : 'none' }">
            <td v-if="selectable" class="bb-table-column">
              <Checkbox v-model="checkValue" color="teal" :value="child.id" value-off="0" :disabled="disabled" @change="valueChange" />
            </td>
            <slot name="child" :child="child" />
          </BBTBody>
        </template>
      </template>
      <template v-if="$slots['footer-prepend']">
        <slot name="footer-prepend"></slot>
      </template>
      <BBTFooter v-if="showFooter && !!header.length" :selectable="selectable" :header="header" :header-key="headerKey" :footer="footer" :footer-key="footerKey" :footer-value="footerValue" :footer-align="footerAlign">
        <template #default="scoped">
          <slot name="footer" :column="scoped"></slot>
        </template>
      </BBTFooter>
    </table>
  </div>
</template>

<script>
import { defineComponent, onMounted, ref, watch } from 'vue'
import { deepClone } from 'utils/deep-clone'

// Component
import BBTHead from 'components/Table/BBTHead.vue'
import BBTBody from 'components/Table/BBTBody.vue'
import BBTFooter from 'components/Table/BBTFooter.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'

export default defineComponent({
  components: {
    BBTHead,
    BBTBody,
    BBTFooter,
    Checkbox
  },
  props: {
    loading: {
      type: Boolean,
      default: false
    },
    selectable: {
      type: Boolean,
      default: false,
    },
    header: {
      type: Array,
      default: () => [],
    },
    body: {
      type: Array,
      default: () => [],
    },
    items: {
      type: Array,
      default: () => [],
    },
    children: {
      type: String,
      default: '',
    },
    checkboxValue: {
      type: String,
      default: 'id',
    },
    checkAll: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    collapseAll: {
      type: Boolean,
      default: false
    },
    showFooter: {
      type: Boolean,
      default: false
    },
    footer: {
      type: Array,
      default: () => ([])
    },
    footerKey: {
      type: String,
      default: 'key'
    },
    footerValue: {
      type: String,
      default: 'value'
    },
    headerKey: {
      type: String,
      default: 'value'
    },
    footerAlign: {
      type: String,
      default: 'left'
    },
    disableExpand: {
      type: Boolean,
      default: false
    }
  },
  emits: ['selection-change', 'sort'],
  setup(props, { emit }) {
    const action = ref([])
    const itemsId = ref([])
    const checkValue = ref([])
    const total = ref(0)
    const isCheckAll = ref(props.checkAll)

    props.items.map(item => {
      action.value.push(false)
      if (props.children && item[props.children].length > 0) {
        item.child.map(child => {
          itemsId.value.push(child.id)
        })

        return
      }

      itemsId.value.push(item.id)
    })

    total.value = itemsId.value.length

    const checkAll = value => {
      checkValue.value = []

      if (value) {
        checkValue.value = itemsId.value
      }

      emitChange()
    }

    const valueChange = () => {
      emitChange()
    }

    const mapData = () => {
      const id = deepClone(checkValue.value)

      const data = props.items.map(item => {
        if (props.children && item[props.children].length > 0) {
          return item.child.map(child => {
            return child
          })
        }

        return item
      }).flat().filter(element => {
        return id.find(value => value == element.id)
      })

      return data
    }

    const emitChange = () => {
      const data = mapData()
      emit('selection-change', data)
    }

    const handleSort = (data) => {
      emit('sort', data)
    }

    watch(() =>  props.checkAll, () => {
      if(props.checkAll) checkAll(true)
    }, {
      immediate: true
    })

    onMounted(() => {
      const isCollapse = props.collapseAll
      if(isCollapse) action.value = action.value.map((_) => true)
    })

    watch(() => ({...props.items}), () => {
      itemsId.value = []
      props.items.map(item => {
        action.value.push(false)
        if (props.children && item[props.children].length > 0) {
          item.child.map(child => {
            itemsId.value.push(child.id)
          })

          return
        }

        itemsId.value.push(item.id)
      })

      // Filter and remove checked id that doesn't exist in the data
      checkValue.value = itemsId.value.filter(id => checkValue.value.includes(id))
      total.value = itemsId.value.length
    })

    return {
      action,
      isCheckAll,
      checkValue,
      checkAll,
      valueChange,
      total,
      handleSort
    }
  }
})
</script>

<style lang="scss">
.bb-table {
  @apply min-w-full;

  display: table;

  .bb-table-row {
    display: table-row;

    .bb-table-header, .bb-table-footer {
      @apply font-bold px-4 py-6;

      display: table-cell;
      background-color: #f2f2f2;
      border-top: none;
    }

    .bb-table-footer{
      background-color: #f5f7fa;
      &.--align-right{
        @apply text-right;
      }
      &.--align-center{
        @apply text-center;
      }
    }

    .bb-table-column {
      @apply px-4 py-6;

      display: table-cell;
      border-top: 1px solid #ebeef5;
    }
  }
}
.arrow {
  clip-path: polygon(90% 50%, 20% 100%, 20% 0);
}
</style>