<template>
  <tr class="bb-table-row">
    <td v-if="selectable" class="bb-table-footer"></td>
    <template v-if="!hasFooterValues">
      <thead v-for="head in header" :key="head" class="bb-table-footer">
        <slot :key="head"></slot>
      </thead>
    </template>
    <template v-else>
      <td v-for="footer in footerValues" :class="['bb-table-footer', footerAlignment, footer?.verticalAlign === 'bottom' && '--vertical-bottom']" :key="footer">
        <slot :footer="footer">
          {{footer && footer[footerValue]}}
        </slot>
      </td>
    </template>
  </tr>
</template>

<script>
import { defineComponent, computed } from 'vue'

export default defineComponent({
  props: {
    selectable: {
      type: Boolean,
      default: false,
    },
    header: {
      type: Array,
      default: () => [],
    },
    total: {
      type: Number,
      default: 0,
    },
    footer: {
      type: Array,
      default: () => ([])
    },
    footerKey: {
      type: String,
      default: 'key'
    },
    footerValue: {
      type: String,
      default: 'value'
    },
    footerAlign: {
      type: String,
      default: 'left'
    },
    headerKey: {
      type: String,
      default: 'key'
    }
  },
  setup(props) {
    const footerValues = computed(() => {
      const headers = props.header
      const footers = props.footer
      const footerKey = props.footerKey
      const footerValue = props.footerValue
      const headerKey = props.headerKey
      return headers.map(header => {
        const value = footers.find(footer => (footer.hasOwnProperty(footerKey) && footer.hasOwnProperty(footerValue)) && footer[footerKey] === header[headerKey])
        if(!!value) return value
        return null
      })
    })
    const hasFooterValues = computed(() => footerValues.value.filter(value => value !== null).length > 0)
    const footerAlignment = computed(() => ['left', 'right', 'center'].includes(props.footerAlign) && `--align-${props.footerAlign}` || '--align-left')

    return {
      footerValues,
      footerAlignment,
      hasFooterValues
    }
  }
})
</script>
<style lang="scss">
.--vertical-bottom{
  vertical-align: bottom;
}
</style>