<template>
  <tr class="bb-table-row">
    <td v-if="selectable" class="bb-table-header">
      <Checkbox color="teal" :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAll" value="1" value-off="0" :disabled="disabled" />
    </td>
    <td v-for="head in header" class="bb-table-header el-table" :class="`text-${head.align ? head.align : 'left'}`" :key="head">
      {{ head.label ? head.label : head }}
      <span v-if="head?.sortable" class="caret-wrapper">
        <i class="sort-caret ascending" :style="handleSortActive(head, head?.sortby, 'asc') ? 'border-bottom-color: #409EFF;' : ''" @click="handleSortAsc(head?.sortby)"></i>
        <i class="sort-caret descending" :style="handleSortActive(head, head?.sortby, 'desc')? 'border-top-color: #409EFF;' : ''" @click="handleSortDesc(head?.sortby)"></i>
      </span>
    </td>
  </tr>
</template>

<script>
import { defineComponent, ref, reactive, watch } from 'vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'

export default defineComponent({
  components: {
    Checkbox,
  },
  props: {
    selectable: {
      type: Boolean,
      default: false,
    },
    header: {
      type: Array,
      default: () => [],
    },
    value: {
      type: Array,
      default: () => [],
    },
    total: {
      type: Number,
      default: 0,
    },
    checkAll: {
      type: Boolean,
      deafult: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const checkAll = ref(props.checkAll)
    const isIndeterminate = ref(!!props.checkAll ? false : true)

    const sortable = reactive({
      asc: false,
      desc: false,
    })

    const handleCheckAll = () => {
      emit('check-all', checkAll.value)
    }

    watch(() => ({...props.value}), () => {
      isIndeterminate.value = true
      checkAll.value = true

      if (props.value.length == props.total) {
        isIndeterminate.value = false
      }

      if (props.value.length == 0) {
        checkAll.value = false
      }
    })

    const handleSortAsc = (key) => {
      sortable.asc = !sortable.asc
      sortable.desc = false
      emit('sortTHead', {
        sortby: key, 
        sortType: sortable.asc ? 'asc' : ''
      })
    }

    const handleSortDesc = (key) => {
      sortable.desc = !sortable.desc
      sortable.asc = false
      emit('sortTHead', {
        sortby: key, 
        sortType: sortable.desc ? 'desc' : ''
      })
    }

    const handleSortActive = (data, sortBy, orderBy) => {
      return data.sortActive.sort_by == sortBy && data.sortActive.order_by == orderBy
    }


    return {
      checkAll,
      isIndeterminate,
      handleCheckAll,
      sortable,
      handleSortAsc,
      handleSortDesc,
      handleSortActive
    }
  }
})
</script>

<style lang="scss" scoped>
  .el-table {
    width: unset;
  }
</style>
